___CSS_LOADER_EXPORT___.push([module.id, ".hearth::hover{color:red}", "",{"version":3,"sources":["webpack://./resources/js/vue/App.vue"],"names":[],"mappings":"AA+GA,eACI,SACJ","sourcesContent":["<template>\n    <div class=\"container\">\n\n        <div class=\"row\">\n            <aside class=\"col-lg-3\">\n                <h3>\n                    Category\n                </h3>\n                <div class=\"card\">\n                    <ul class=\"list-group list-group-flush\">\n                        <li class=\"list-group-item\" v-for=\"category in categories\">\n                            <a v-on:click=\"setCategory(category.id)\">{{ category.name }}</a>\n                        </li>\n                    </ul>\n                </div>\n            </aside>\n\n            <div class=\"col-lg-6 \">\n\n                <article class=\"card p-2\" v-for=\"article in articles\">\n\n                    <img class=\"card-img-top\"  :src=\"article.image\" alt=\"Card image cap\">\n                    <div class=\"card-body\">\n                        <h5 class=\"card-title\">\n                            {{article.title}}\n                        </h5>\n                        <p class=\"card-text\">This is a wider card with supporting text below as a natural lead-in to additional content. This content is a little bit longer.</p>\n                        <p class=\"card-text\"><small class=\"text-muted\">Last updated 3 mins ago</small></p>\n                    </div>\n\n\n                </article>\n\n            </div>\n\n            <aside class=\"col-lg-3\">\n                <h3>\n                    Tags\n                </h3>\n                <div class=\"card\">\n\n                    <ul class=\"list-group list-group-flush\">\n                        <li class=\"list-group-item\" v-for=\"category in categories\">\n                            <a v-on:click=\"setCategory(category.id)\">{{ category.name }}</a>\n                        </li>\n                    </ul>\n                </div>\n\n            </aside>\n\n        </div>\n\n\n    </div>\n\n\n</template>\n<script>\nconsole.log('App.vue');\nexport default {\n    name: 'App',\n    data() {\n        return {\n            message: this.message,\n            errored: false,\n            articles: [],\n            categories: [],\n            filterCategories: [],\n            page: 1,\n            query: {\n                limit: 10,\n                page: 1\n            },\n        }\n    },\n    methods: {\n        handleLoadMore($state) {\n\n            axios\n                .get('api/v1/articles?page=' + this.page + '&category=' + this.filterCategories)\n                .then(response => {\n\n                    this.articles = this.articles.concat(response.data.articles);\n                    this.categories = response.data.categories;\n                    this.page++;\n                    $state.loaded();\n                })\n                .catch(error => {\n                    console.log(error)\n                    this.errored = true\n                })\n                .finally(() => {\n                    $state.loaded()\n                })\n\n        },\n        showArticle(article) {\n            console.log(article)\n        },\n        setCategory(category) {\n            this.filterCategories.push(category);\n            console.log(category)\n        }\n    },\n    mounted() {\n        console.log(\"Welcome to the Home Page\");\n        this.handleLoadMore(this.$refs.infiniteLoading);\n    }\n}\n</script>\n<style>\n.hearth::hover{\n    color: red;\n}\n</style>\n"],"sourceRoot":""}]);
